<template>
  <div class="hs-container">
    <c-header :title="$t('base.AboutUs')" isBackNav></c-header>
    <hs-scroll class="table-view">
      <ul>
        <div class="company-logo" style="background-image: url('static/image/personal-center/about/logo.png')"></div>
        <div class="des">{{$t('base.AboutUsDes')}}</div>
        <br>
        <div class="des">
          <div class="logo" style="background-image: url('static/image/personal-center/contect/phone.png')"></div>
          <p>{{$t('base.NationalServicePhone')}}: 4006-123-123</p>
        </div>
        <div class="des">
          <div class="logo" style="background-image: url('static/image/personal-center/contect/address.png')"></div>
          <p>{{$t('base.AddressDes')}}</p>
        </div>
      </ul>
    </hs-scroll>
    <div class="footer">
      <p>Copyright@{{beTime(new Date(), 'yyyy')}}</p>
      <p>恒顺旅行homsom.com版权所有</p>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  components: {
    CHeader,
    HsScroll
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 80px)
    overflow hidden
    background-color $color_white
    .company-logo
      width 140px
      height 70px
      background-size 100% 100%
      margin 40px auto 50px auto
    .des
      @extend .flex-row
      width 100%
      padding 0 30px
      font-size 0.8rem
      color $color_black_text_0
      line-height 1.8em
      text-align justify
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 5px
        margin-top 3px
      >p
        width calc(100% - 25px)
  >.footer
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 80px
    background-color $color_white
    >p
      font-size 0.8rem
      color $color_gray_text
      &:first-child
        margin-bottom 6px
</style>



